/**
 * HTTP Server for MCP
 * Provides REST API for React Native app communication
 *
 * @see https://github.com/benjitaylor/agentation/blob/main/mcp/src/server/http.ts
 */

import express, { Express, Request, Response } from 'express';
import cors from 'cors';
import type Database from 'better-sqlite3';
import type {
  Annotation,
  Session,
  ActionRequest,
  ActionResponse,
  HealthCheckResponse,
} from '../types';
import {
  createSession,
  getSession,
  getSessionWithAnnotations,
  listSessions,
  findSessionByUrl,
  upsertAnnotation,
  getAnnotation,
  updateAnnotation,
  deleteAnnotation,
  getPendingAnnotations,
  getAllPendingAnnotations,
} from './sqlite';
import { generateId } from '../utils';

const VERSION = '0.1.0';

// =============================================================================
// Event Emitter for Action Requests
// =============================================================================

type ActionListener = (request: ActionRequest) => void;
const actionListeners = new Map<string, ActionListener[]>();

export function onAction(sessionId: string, listener: ActionListener): () => void {
  const listeners = actionListeners.get(sessionId) ?? [];
  listeners.push(listener);
  actionListeners.set(sessionId, listeners);

  return () => {
    const updated = (actionListeners.get(sessionId) ?? []).filter((l) => l !== listener);
    if (updated.length === 0) {
      actionListeners.delete(sessionId);
    } else {
      actionListeners.set(sessionId, updated);
    }
  };
}

function emitAction(request: ActionRequest): void {
  const listeners = actionListeners.get(request.sessionId) ?? [];
  listeners.forEach((listener) => listener(request));
}

// =============================================================================
// Webhook Support
// =============================================================================

async function sendWebhooks(
  urls: string[],
  payload: unknown
): Promise<void> {
  for (const url of urls) {
    try {
      await fetch(url, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });
    } catch (error) {
      console.error(`Webhook failed for ${url}:`, error);
    }
  }
}

// =============================================================================
// HTTP Server
// =============================================================================

export function createHttpServer(
  db: Database.Database,
  webhookUrls: string[] = []
): Express {
  const app = express();

  app.use(cors());
  app.use(express.json());

  // Health check
  app.get('/health', (_req: Request, res: Response) => {
    const response: HealthCheckResponse = { status: 'ok', version: VERSION };
    res.json(response);
  });

  // ==========================================================================
  // Session Endpoints
  // ==========================================================================

  // List sessions
  app.get('/sessions', (req: Request, res: Response) => {
    const status = req.query.status as string | undefined;
    const limit = parseInt(req.query.limit as string) || 100;

    const sessions = listSessions(
      db,
      status as Session['status'] | undefined,
      limit
    );
    res.json(sessions);
  });

  // Create session
  app.post('/sessions', (req: Request, res: Response) => {
    const { url, projectId, metadata } = req.body;

    if (!url) {
      res.status(400).json({ error: 'url is required' });
      return;
    }

    // Check if session already exists for this URL
    const existing = findSessionByUrl(db, url);
    if (existing) {
      res.json(existing);
      return;
    }

    const id = generateId();
    const session = createSession(db, id, url, projectId, metadata);
    res.status(201).json(session);
  });

  // Get session
  app.get('/sessions/:sessionId', (req: Request, res: Response) => {
    const { sessionId } = req.params;
    const session = getSessionWithAnnotations(db, sessionId);

    if (!session) {
      res.status(404).json({ error: 'Session not found' });
      return;
    }

    res.json(session);
  });

  // ==========================================================================
  // Annotation Endpoints
  // ==========================================================================

  // Sync annotation (create or update)
  app.post('/sessions/:sessionId/annotations', (req: Request, res: Response) => {
    const { sessionId } = req.params;
    const annotation: Annotation = req.body;

    // Verify session exists
    const session = getSession(db, sessionId);
    if (!session) {
      res.status(404).json({ error: 'Session not found' });
      return;
    }

    // Ensure annotation has required fields
    if (!annotation.id) {
      annotation.id = generateId();
    }
    annotation.sessionId = sessionId;
    annotation.createdAt = annotation.createdAt ?? new Date().toISOString();
    annotation.status = annotation.status ?? 'pending';

    const saved = upsertAnnotation(db, annotation);

    // Fire webhook
    if (webhookUrls.length > 0) {
      sendWebhooks(webhookUrls, {
        event: 'annotation.created',
        timestamp: new Date().toISOString(),
        data: { sessionId, annotation: saved },
      });
    }

    res.status(201).json(saved);
  });

  // Update annotation
  app.patch('/annotations/:annotationId', (req: Request, res: Response) => {
    const { annotationId } = req.params;
    const updates: Partial<Annotation> = req.body;

    const updated = updateAnnotation(db, annotationId, updates);
    if (!updated) {
      res.status(404).json({ error: 'Annotation not found' });
      return;
    }

    // Fire webhook
    if (webhookUrls.length > 0) {
      sendWebhooks(webhookUrls, {
        event: 'annotation.updated',
        timestamp: new Date().toISOString(),
        data: { annotation: updated },
      });
    }

    res.json(updated);
  });

  // Delete annotation
  app.delete('/annotations/:annotationId', (req: Request, res: Response) => {
    const { annotationId } = req.params;

    const annotation = getAnnotation(db, annotationId);
    const deleted = deleteAnnotation(db, annotationId);

    if (!deleted) {
      res.status(404).json({ error: 'Annotation not found' });
      return;
    }

    // Fire webhook
    if (webhookUrls.length > 0 && annotation) {
      sendWebhooks(webhookUrls, {
        event: 'annotation.deleted',
        timestamp: new Date().toISOString(),
        data: { annotation },
      });
    }

    res.status(204).send();
  });

  // Get pending annotations for session
  app.get('/sessions/:sessionId/pending', (req: Request, res: Response) => {
    const { sessionId } = req.params;

    const session = getSession(db, sessionId);
    if (!session) {
      res.status(404).json({ error: 'Session not found' });
      return;
    }

    const pending = getPendingAnnotations(db, sessionId);
    res.json(pending);
  });

  // Get all pending annotations
  app.get('/pending', (_req: Request, res: Response) => {
    const pending = getAllPendingAnnotations(db);
    res.json(pending);
  });

  // ==========================================================================
  // Action Endpoint (for "Send to Agent")
  // ==========================================================================

  app.post('/sessions/:sessionId/action', (req: Request, res: Response) => {
    const { sessionId } = req.params;
    const { annotations, output } = req.body;

    const session = getSession(db, sessionId);
    if (!session) {
      res.status(404).json({ error: 'Session not found' });
      return;
    }

    const actionRequest: ActionRequest = {
      sessionId,
      annotations,
      output,
      timestamp: new Date().toISOString(),
    };

    // Emit to MCP listeners (for wait_for_action)
    emitAction(actionRequest);

    // Fire webhook
    if (webhookUrls.length > 0) {
      sendWebhooks(webhookUrls, {
        event: 'action.requested',
        timestamp: new Date().toISOString(),
        data: actionRequest,
      });
    }

    const response: ActionResponse = {
      success: true,
      actionId: generateId(),
    };

    res.json(response);
  });

  return app;
}

export function startHttpServer(
  app: Express,
  port: number
): Promise<void> {
  return new Promise((resolve) => {
    app.listen(port, () => {
      console.log(`Agentation RN MCP HTTP server running on port ${port}`);
      resolve();
    });
  });
}
