/**
 * MCP Server Implementation
 * Provides tools for Claude Code to interact with annotations
 *
 * @see https://github.com/benjitaylor/agentation/blob/main/mcp/src/server/mcp.ts
 */

import { Server } from '@modelcontextprotocol/sdk/server/index.js';
import { StdioServerTransport } from '@modelcontextprotocol/sdk/server/stdio.js';
import {
  CallToolRequestSchema,
  ListToolsRequestSchema,
} from '@modelcontextprotocol/sdk/types.js';
import type Database from 'better-sqlite3';
import type {
  Annotation,
  ListSessionsInput,
  GetSessionInput,
  GetPendingInput,
  AcknowledgeInput,
  ResolveInput,
  DismissInput,
  ReplyInput,
  WaitForActionInput,
  ActionRequest,
} from '../types';
import {
  listSessions,
  getSessionWithAnnotations,
  getPendingAnnotations,
  getAllPendingAnnotations,
  updateAnnotation,
  getAnnotation,
} from './sqlite';
import { onAction } from './http';
import { generateId } from '../utils';

// =============================================================================
// Tool Definitions
// =============================================================================

const TOOLS = [
  {
    name: 'agentation_list_sessions',
    description: 'List all feedback sessions. Returns sessions with their status and metadata.',
    inputSchema: {
      type: 'object' as const,
      properties: {
        status: {
          type: 'string',
          enum: ['active', 'approved', 'closed'],
          description: 'Filter by session status',
        },
        limit: {
          type: 'number',
          description: 'Maximum number of sessions to return (default: 100)',
        },
      },
    },
  },
  {
    name: 'agentation_get_session',
    description: 'Get a specific session with all its annotations.',
    inputSchema: {
      type: 'object' as const,
      properties: {
        sessionId: {
          type: 'string',
          description: 'The session ID to retrieve',
        },
      },
      required: ['sessionId'],
    },
  },
  {
    name: 'agentation_get_pending',
    description: 'Get all pending (unresolved) annotations for a specific session.',
    inputSchema: {
      type: 'object' as const,
      properties: {
        sessionId: {
          type: 'string',
          description: 'The session ID',
        },
      },
      required: ['sessionId'],
    },
  },
  {
    name: 'agentation_get_all_pending',
    description: 'Get all pending annotations across all sessions.',
    inputSchema: {
      type: 'object' as const,
      properties: {},
    },
  },
  {
    name: 'agentation_acknowledge',
    description: 'Mark an annotation as acknowledged. Use this when you\'ve seen the feedback and are working on it.',
    inputSchema: {
      type: 'object' as const,
      properties: {
        annotationId: {
          type: 'string',
          description: 'The annotation ID to acknowledge',
        },
      },
      required: ['annotationId'],
    },
  },
  {
    name: 'agentation_resolve',
    description: 'Mark an annotation as resolved. Use this when you\'ve addressed the feedback.',
    inputSchema: {
      type: 'object' as const,
      properties: {
        annotationId: {
          type: 'string',
          description: 'The annotation ID to resolve',
        },
        message: {
          type: 'string',
          description: 'Optional resolution message explaining what was done',
        },
      },
      required: ['annotationId'],
    },
  },
  {
    name: 'agentation_dismiss',
    description: 'Dismiss an annotation. Use this for feedback that won\'t be addressed.',
    inputSchema: {
      type: 'object' as const,
      properties: {
        annotationId: {
          type: 'string',
          description: 'The annotation ID to dismiss',
        },
        reason: {
          type: 'string',
          description: 'Reason for dismissing the annotation',
        },
      },
      required: ['annotationId', 'reason'],
    },
  },
  {
    name: 'agentation_reply',
    description: 'Add a reply to an annotation thread. Use this to ask clarifying questions or provide updates.',
    inputSchema: {
      type: 'object' as const,
      properties: {
        annotationId: {
          type: 'string',
          description: 'The annotation ID to reply to',
        },
        message: {
          type: 'string',
          description: 'The reply message',
        },
      },
      required: ['annotationId', 'message'],
    },
  },
  {
    name: 'agentation_wait_for_action',
    description: 'Wait for the user to click "Send to Agent" in the app. Blocks until action is received or timeout.',
    inputSchema: {
      type: 'object' as const,
      properties: {
        sessionId: {
          type: 'string',
          description: 'The session ID to wait on',
        },
        timeoutMs: {
          type: 'number',
          description: 'Timeout in milliseconds (default: 60000)',
        },
      },
      required: ['sessionId'],
    },
  },
];

// =============================================================================
// Tool Handlers
// =============================================================================

function handleListSessions(
  db: Database.Database,
  input: ListSessionsInput
): string {
  const sessions = listSessions(db, input.status, input.limit);
  return JSON.stringify(sessions, null, 2);
}

function handleGetSession(
  db: Database.Database,
  input: GetSessionInput
): string {
  const session = getSessionWithAnnotations(db, input.sessionId);
  if (!session) {
    return JSON.stringify({ error: 'Session not found' });
  }
  return JSON.stringify(session, null, 2);
}

function handleGetPending(
  db: Database.Database,
  input: GetPendingInput
): string {
  const pending = getPendingAnnotations(db, input.sessionId);
  return JSON.stringify(pending, null, 2);
}

function handleGetAllPending(db: Database.Database): string {
  const pending = getAllPendingAnnotations(db);
  return JSON.stringify(pending, null, 2);
}

function handleAcknowledge(
  db: Database.Database,
  input: AcknowledgeInput
): string {
  const updated = updateAnnotation(db, input.annotationId, {
    status: 'acknowledged',
  });

  if (!updated) {
    return JSON.stringify({ error: 'Annotation not found' });
  }

  return JSON.stringify({ success: true, annotation: updated });
}

function handleResolve(
  db: Database.Database,
  input: ResolveInput
): string {
  const annotation = getAnnotation(db, input.annotationId);
  if (!annotation) {
    return JSON.stringify({ error: 'Annotation not found' });
  }

  const thread = annotation.thread ?? [];
  if (input.message) {
    thread.push({
      id: generateId(),
      role: 'agent',
      content: input.message,
      timestamp: Date.now(),
    });
  }

  const updated = updateAnnotation(db, input.annotationId, {
    status: 'resolved',
    resolvedAt: new Date().toISOString(),
    resolvedBy: 'agent',
    thread,
  });

  return JSON.stringify({ success: true, annotation: updated });
}

function handleDismiss(
  db: Database.Database,
  input: DismissInput
): string {
  const annotation = getAnnotation(db, input.annotationId);
  if (!annotation) {
    return JSON.stringify({ error: 'Annotation not found' });
  }

  const thread = annotation.thread ?? [];
  thread.push({
    id: generateId(),
    role: 'agent',
    content: `Dismissed: ${input.reason}`,
    timestamp: Date.now(),
  });

  const updated = updateAnnotation(db, input.annotationId, {
    status: 'dismissed',
    thread,
  });

  return JSON.stringify({ success: true, annotation: updated });
}

function handleReply(
  db: Database.Database,
  input: ReplyInput
): string {
  const annotation = getAnnotation(db, input.annotationId);
  if (!annotation) {
    return JSON.stringify({ error: 'Annotation not found' });
  }

  const thread = annotation.thread ?? [];
  thread.push({
    id: generateId(),
    role: 'agent',
    content: input.message,
    timestamp: Date.now(),
  });

  const updated = updateAnnotation(db, input.annotationId, { thread });

  return JSON.stringify({ success: true, annotation: updated });
}

async function handleWaitForAction(
  input: WaitForActionInput
): Promise<string> {
  const timeout = input.timeoutMs ?? 60000;

  return new Promise((resolve) => {
    const timer = setTimeout(() => {
      cleanup();
      resolve(JSON.stringify({ timeout: true, message: 'No action received within timeout' }));
    }, timeout);

    const cleanup = onAction(input.sessionId, (request: ActionRequest) => {
      clearTimeout(timer);
      resolve(JSON.stringify(request, null, 2));
    });
  });
}

// =============================================================================
// MCP Server
// =============================================================================

export function createMcpServer(db: Database.Database): Server {
  const server = new Server(
    {
      name: 'agentation-rn-mcp',
      version: '0.1.0',
    },
    {
      capabilities: {
        tools: {},
      },
    }
  );

  // List available tools
  server.setRequestHandler(ListToolsRequestSchema, async () => {
    return { tools: TOOLS };
  });

  // Handle tool calls
  server.setRequestHandler(CallToolRequestSchema, async (request) => {
    const { name, arguments: args } = request.params;

    try {
      let result: string;

      switch (name) {
        case 'agentation_list_sessions':
          result = handleListSessions(db, args as ListSessionsInput);
          break;
        case 'agentation_get_session':
          result = handleGetSession(db, args as GetSessionInput);
          break;
        case 'agentation_get_pending':
          result = handleGetPending(db, args as GetPendingInput);
          break;
        case 'agentation_get_all_pending':
          result = handleGetAllPending(db);
          break;
        case 'agentation_acknowledge':
          result = handleAcknowledge(db, args as AcknowledgeInput);
          break;
        case 'agentation_resolve':
          result = handleResolve(db, args as ResolveInput);
          break;
        case 'agentation_dismiss':
          result = handleDismiss(db, args as DismissInput);
          break;
        case 'agentation_reply':
          result = handleReply(db, args as ReplyInput);
          break;
        case 'agentation_wait_for_action':
          result = await handleWaitForAction(args as WaitForActionInput);
          break;
        default:
          result = JSON.stringify({ error: `Unknown tool: ${name}` });
      }

      return {
        content: [{ type: 'text', text: result }],
      };
    } catch (error) {
      const message = error instanceof Error ? error.message : String(error);
      return {
        content: [{ type: 'text', text: JSON.stringify({ error: message }) }],
        isError: true,
      };
    }
  });

  return server;
}

export async function startMcpServer(server: Server): Promise<void> {
  const transport = new StdioServerTransport();
  await server.connect(transport);
  console.error('Agentation RN MCP server running on stdio');
}
