/**
 * SQLite Storage for MCP Server
 * Persists sessions and annotations to disk
 *
 * @see https://github.com/benjitaylor/agentation/blob/main/mcp/src/server/sqlite.ts
 */

import Database from 'better-sqlite3';
import { existsSync, mkdirSync } from 'fs';
import { dirname, resolve } from 'path';
import { homedir } from 'os';
import type { Annotation, Session, SessionWithAnnotations, SessionStatus } from '../types';

// =============================================================================
// Database Setup
// =============================================================================

function expandPath(path: string): string {
  if (path.startsWith('~')) {
    return path.replace('~', homedir());
  }
  return resolve(path);
}

function ensureDir(filePath: string): void {
  const dir = dirname(filePath);
  if (!existsSync(dir)) {
    mkdirSync(dir, { recursive: true });
  }
}

export function createDatabase(dbPath: string): Database.Database {
  const expandedPath = expandPath(dbPath);
  ensureDir(expandedPath);

  const db = new Database(expandedPath);

  // Enable WAL mode for better concurrent access
  db.pragma('journal_mode = WAL');

  // Create tables
  db.exec(`
    CREATE TABLE IF NOT EXISTS sessions (
      id TEXT PRIMARY KEY,
      url TEXT NOT NULL,
      status TEXT NOT NULL DEFAULT 'active',
      created_at TEXT NOT NULL,
      updated_at TEXT,
      project_id TEXT,
      metadata TEXT
    );

    CREATE TABLE IF NOT EXISTS annotations (
      id TEXT PRIMARY KEY,
      session_id TEXT NOT NULL,
      data TEXT NOT NULL,
      created_at TEXT NOT NULL,
      updated_at TEXT,
      FOREIGN KEY (session_id) REFERENCES sessions(id) ON DELETE CASCADE
    );

    CREATE INDEX IF NOT EXISTS idx_annotations_session ON annotations(session_id);
    CREATE INDEX IF NOT EXISTS idx_sessions_status ON sessions(status);
    CREATE INDEX IF NOT EXISTS idx_sessions_url ON sessions(url);
  `);

  return db;
}

// =============================================================================
// Session Operations
// =============================================================================

export function createSession(
  db: Database.Database,
  id: string,
  url: string,
  projectId?: string,
  metadata?: Record<string, unknown>
): Session {
  const now = new Date().toISOString();

  const stmt = db.prepare(`
    INSERT INTO sessions (id, url, status, created_at, project_id, metadata)
    VALUES (?, ?, 'active', ?, ?, ?)
  `);

  stmt.run(id, url, now, projectId ?? null, metadata ? JSON.stringify(metadata) : null);

  return {
    id,
    url,
    status: 'active',
    createdAt: now,
    projectId,
    metadata,
  };
}

export function getSession(
  db: Database.Database,
  sessionId: string
): Session | null {
  const stmt = db.prepare(`
    SELECT id, url, status, created_at, updated_at, project_id, metadata
    FROM sessions
    WHERE id = ?
  `);

  const row = stmt.get(sessionId) as {
    id: string;
    url: string;
    status: SessionStatus;
    created_at: string;
    updated_at: string | null;
    project_id: string | null;
    metadata: string | null;
  } | undefined;

  if (!row) return null;

  return {
    id: row.id,
    url: row.url,
    status: row.status,
    createdAt: row.created_at,
    updatedAt: row.updated_at ?? undefined,
    projectId: row.project_id ?? undefined,
    metadata: row.metadata ? JSON.parse(row.metadata) : undefined,
  };
}

export function getSessionWithAnnotations(
  db: Database.Database,
  sessionId: string
): SessionWithAnnotations | null {
  const session = getSession(db, sessionId);
  if (!session) return null;

  const annotations = getAnnotationsForSession(db, sessionId);

  return {
    ...session,
    annotations,
  };
}

export function listSessions(
  db: Database.Database,
  status?: SessionStatus,
  limit: number = 100
): Session[] {
  let query = `
    SELECT id, url, status, created_at, updated_at, project_id, metadata
    FROM sessions
  `;

  const params: unknown[] = [];

  if (status) {
    query += ' WHERE status = ?';
    params.push(status);
  }

  query += ' ORDER BY created_at DESC LIMIT ?';
  params.push(limit);

  const stmt = db.prepare(query);
  const rows = stmt.all(...params) as Array<{
    id: string;
    url: string;
    status: SessionStatus;
    created_at: string;
    updated_at: string | null;
    project_id: string | null;
    metadata: string | null;
  }>;

  return rows.map((row) => ({
    id: row.id,
    url: row.url,
    status: row.status,
    createdAt: row.created_at,
    updatedAt: row.updated_at ?? undefined,
    projectId: row.project_id ?? undefined,
    metadata: row.metadata ? JSON.parse(row.metadata) : undefined,
  }));
}

export function updateSessionStatus(
  db: Database.Database,
  sessionId: string,
  status: SessionStatus
): boolean {
  const stmt = db.prepare(`
    UPDATE sessions
    SET status = ?, updated_at = ?
    WHERE id = ?
  `);

  const result = stmt.run(status, new Date().toISOString(), sessionId);
  return result.changes > 0;
}

export function findSessionByUrl(
  db: Database.Database,
  url: string
): Session | null {
  const stmt = db.prepare(`
    SELECT id, url, status, created_at, updated_at, project_id, metadata
    FROM sessions
    WHERE url = ? AND status = 'active'
    ORDER BY created_at DESC
    LIMIT 1
  `);

  const row = stmt.get(url) as {
    id: string;
    url: string;
    status: SessionStatus;
    created_at: string;
    updated_at: string | null;
    project_id: string | null;
    metadata: string | null;
  } | undefined;

  if (!row) return null;

  return {
    id: row.id,
    url: row.url,
    status: row.status,
    createdAt: row.created_at,
    updatedAt: row.updated_at ?? undefined,
    projectId: row.project_id ?? undefined,
    metadata: row.metadata ? JSON.parse(row.metadata) : undefined,
  };
}

// =============================================================================
// Annotation Operations
// =============================================================================

export function upsertAnnotation(
  db: Database.Database,
  annotation: Annotation
): Annotation {
  const now = new Date().toISOString();
  const data = JSON.stringify(annotation);

  const stmt = db.prepare(`
    INSERT INTO annotations (id, session_id, data, created_at, updated_at)
    VALUES (?, ?, ?, ?, ?)
    ON CONFLICT(id) DO UPDATE SET
      data = excluded.data,
      updated_at = excluded.updated_at
  `);

  stmt.run(
    annotation.id,
    annotation.sessionId,
    data,
    annotation.createdAt ?? now,
    now
  );

  return annotation;
}

export function getAnnotation(
  db: Database.Database,
  annotationId: string
): Annotation | null {
  const stmt = db.prepare(`
    SELECT data FROM annotations WHERE id = ?
  `);

  const row = stmt.get(annotationId) as { data: string } | undefined;
  if (!row) return null;

  return JSON.parse(row.data);
}

export function getAnnotationsForSession(
  db: Database.Database,
  sessionId: string
): Annotation[] {
  const stmt = db.prepare(`
    SELECT data FROM annotations
    WHERE session_id = ?
    ORDER BY created_at ASC
  `);

  const rows = stmt.all(sessionId) as Array<{ data: string }>;
  return rows.map((row) => JSON.parse(row.data));
}

export function updateAnnotation(
  db: Database.Database,
  annotationId: string,
  updates: Partial<Annotation>
): Annotation | null {
  const existing = getAnnotation(db, annotationId);
  if (!existing) return null;

  const updated: Annotation = {
    ...existing,
    ...updates,
    updatedAt: new Date().toISOString(),
  };

  return upsertAnnotation(db, updated);
}

export function deleteAnnotation(
  db: Database.Database,
  annotationId: string
): boolean {
  const stmt = db.prepare('DELETE FROM annotations WHERE id = ?');
  const result = stmt.run(annotationId);
  return result.changes > 0;
}

export function getPendingAnnotations(
  db: Database.Database,
  sessionId: string
): Annotation[] {
  const annotations = getAnnotationsForSession(db, sessionId);
  return annotations.filter((a) => a.status === 'pending' || !a.status);
}

export function getAllPendingAnnotations(db: Database.Database): Annotation[] {
  const stmt = db.prepare(`
    SELECT data FROM annotations
    ORDER BY created_at ASC
  `);

  const rows = stmt.all() as Array<{ data: string }>;
  const annotations = rows.map((row) => JSON.parse(row.data) as Annotation);

  return annotations.filter((a) => a.status === 'pending' || !a.status);
}
