/**
 * MCP Server Types
 * Shared types between MCP server and clients
 *
 * @see https://github.com/benjitaylor/agentation/blob/main/mcp/src/types.ts
 */

// =============================================================================
// Annotation Types
// =============================================================================

export type AnnotationIntent = 'fix' | 'change' | 'question' | 'approve';
export type AnnotationSeverity = 'blocking' | 'important' | 'suggestion';
export type AnnotationStatus = 'pending' | 'acknowledged' | 'resolved' | 'dismissed';

export type ThreadMessage = {
  id: string;
  role: 'human' | 'agent';
  content: string;
  timestamp: number;
};

export type Annotation = {
  id: string;
  x: number;
  y: number;
  comment: string;
  element: string;
  elementPath: string;
  timestamp: number;
  selectedText?: string;
  boundingBox?: { x: number; y: number; width: number; height: number };
  nearbyText?: string;
  cssClasses?: string;
  nearbyElements?: string;
  computedStyles?: string;
  fullPath?: string;
  accessibility?: string;
  isMultiSelect?: boolean;
  isFixed?: boolean;
  reactComponents?: string;
  elementBoundingBoxes?: Array<{ x: number; y: number; width: number; height: number }>;

  // Protocol fields
  sessionId?: string;
  url?: string;
  intent?: AnnotationIntent;
  severity?: AnnotationSeverity;
  status?: AnnotationStatus;
  thread?: ThreadMessage[];
  createdAt?: string;
  updatedAt?: string;
  resolvedAt?: string;
  resolvedBy?: 'human' | 'agent';
  authorId?: string;

  // RN-specific fields
  routeName?: string;
  componentType?: string;
  sourcePath?: string;
  lineNumber?: number;
  columnNumber?: number;
  testID?: string;
  platform?: 'ios' | 'android';

  // Sync tracking
  _syncedTo?: string;
};

// =============================================================================
// Session Types
// =============================================================================

export type SessionStatus = 'active' | 'approved' | 'closed';

export type Session = {
  id: string;
  url: string;
  status: SessionStatus;
  createdAt: string;
  updatedAt?: string;
  projectId?: string;
  metadata?: Record<string, unknown>;
};

export type SessionWithAnnotations = Session & {
  annotations: Annotation[];
};

// =============================================================================
// API Types
// =============================================================================

export type ActionRequest = {
  sessionId: string;
  annotations: Annotation[];
  output: string;
  timestamp: string;
};

export type ActionResponse = {
  success: boolean;
  message?: string;
  actionId?: string;
};

export type HealthCheckResponse = {
  status: 'ok';
  version: string;
};

// =============================================================================
// MCP Tool Input Types
// =============================================================================

export type ListSessionsInput = {
  status?: SessionStatus;
  limit?: number;
};

export type GetSessionInput = {
  sessionId: string;
};

export type GetPendingInput = {
  sessionId: string;
};

export type AcknowledgeInput = {
  annotationId: string;
};

export type ResolveInput = {
  annotationId: string;
  message?: string;
};

export type DismissInput = {
  annotationId: string;
  reason: string;
};

export type ReplyInput = {
  annotationId: string;
  message: string;
};

export type WaitForActionInput = {
  sessionId: string;
  timeoutMs?: number;
};

// =============================================================================
// Server Configuration
// =============================================================================

export type ServerConfig = {
  port: number;
  dbPath: string;
  webhookUrls?: string[];
};

export const DEFAULT_CONFIG: ServerConfig = {
  port: 4848,
  dbPath: '~/.agentation-rn/store.db',
};
